import PropTypes from "prop-types";
import React, { Component } from "react";
import { DropdownItem, DropdownMenu, DropdownToggle, Nav } from "reactstrap";
import { connect } from "react-redux";
import { AppRoutes } from "../../Config";
import UserIcon from "./../../assets/avatars/user-default.png";
import { AppHeaderDropdown, AppSidebarToggler, AppNavbarBrand } from "@coreui/react";
const propTypes = {
   children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
   constructor(props) {
      super(props);
      this.state = {
         dropdownOpen: false
      };
   }
   toggle = () => {
      this.setState(prevState => ({
         dropdownOpen: !prevState.dropdownOpen
      }));
   };
   onMouseEnter = () => {
      this.setState({ dropdownOpen: true });
   };
   onMouseLeave = () => {
      this.setState({ dropdownOpen: false });
   };
   onGoPage = () => {
      let pageUrl = "http://serviceadvisor.io:8001";
      window.open(pageUrl, "_blank");
   };
   onGoHome = () => {
      let pageUrl = "http://serviceadvisor.io";
      window.open(pageUrl, "_blank");
   };
   render() {
      // eslint-disable-next-line
      const { profileData } = this.props;
      const { profileInfo } = profileData;
      let { userDetails } = this.props;
      if (!userDetails) {
         userDetails = {};
      }
      let isActive = this.props.history.location.pathname === "/profile" ? "/profile" : "";
      let className = isActive === "/profile" ? "header-active" : "";
      return (
         <React.Fragment>
            <div className="custom-main-logo">
               <AppSidebarToggler className="d-lg-none" display="md" mobile />
               <AppNavbarBrand
                  className={"cursor_pointer pl-3"}
                  // onClick={() => this.onGoPage()}
               >
               <h2>DEEP PLAY</h2>
               </AppNavbarBrand>
               {/* <AppSidebarToggler
                  children={<i className="fa fa-bars" />}
                  className="custom-toggle-bar custom-toggle-ba d-md-down-none"
                  display="lg"
               /> */}
               <Nav className="ml-auto" navbar>
                  <AppHeaderDropdown direction="down">
                     <DropdownToggle nav >
                        <div className={"pr-3"}>
                           <span className="mr-2">
                              <img
                                 src={UserIcon}
                                 className="img-avatar"
                                 alt="superadmin@deepplay.org"
                              />
                           </span>
                           <span className="font-weight-bold mr-2">
                              {profileInfo.firstName ? [profileInfo.firstName, profileInfo.lastName].join(" ").trim() : null}
                           </span>
                           <span className="position-relative" style={{top:"2px"}}>
                              <i className="icons icon-arrow-down" />
                           </span>
                        </div>
                     </DropdownToggle>
                     <DropdownMenu right style={{ right: "auto" }}>
                        {/* <DropdownItem onClick={e => this.onGoHome(e)}>
                           <i className="fa fa-chrome" /> Go to Front Website
                        </DropdownItem> */}
                        <DropdownItem
                           onClick={() => {
                              this.props.history.push(AppRoutes.PROFILE.url);
                           }}
                           className={className ? className : ""}
                        >
                           <i className={className ? "fas fa-user text-white" : "fas fa-user"} /> My Profile
                        </DropdownItem>
                        <DropdownItem onClick={e => this.props.onLogout(e)}>
                           <i className="fas fa-lock" /> Logout
                        </DropdownItem>
                     </DropdownMenu>
                  </AppHeaderDropdown>
               </Nav>
            </div>
         </React.Fragment>
      );
   }
}
const mapStateToProps = state => ({
   profileData: state.profileInfoReducer
});

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default connect(
   mapStateToProps,
   undefined
)(DefaultHeader);
